<template>
  <Loading :isShowLoading="isShowLoading"></Loading>
  <div class="warVideo">
    <div class="wrapper">
      <!--  -->
      <jsVideo
        :key="videoUrl"
        :videoExtend="videoExtend"
        :videoUrl="videoUrl"
        @handleExtend="handleExtend"
        @handleScale="handleScale"
      ></jsVideo>
      <!--  -->
      <div class="anthology" :class="anthologyCut ? 'anthologyHide' : ''">
        <!--  -->
        <div class="title_box">
          <div class="title">
            <h2>
              <span></span>
            </h2>
          </div>
        </div>
        <!--  -->
        <div class="collection">
          <ul>
            <li
              class="select-item"
              :class="item.selected ? 'selected' : ''"
              v-for="(item, index) in videoData"
              :key="item.id"
              @click="handleChangeAnthology(item)"
            >
              <div class="select-title">
                <span>{{ "0" + (index + 1) }}</span>
                <a href="javascript:;">{{ item.name }}</a>
              </div>
              <i :class="item.selected ? 'playon-icon' : ''"></i>
            </li>
          </ul>
        </div>
        <!--  -->
        <div class="synopsis">
          <span></span>
          <p>
            1945
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="tips">
      <p>
        
      </p>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import jsVideo from "./components/jsVideo.vue";
import { useRoute } from "vue-router";
import Loading from "@/views/Loading/Loading.vue";
import { getVideo } from "@/api/video.js";

export default {
  setup() {
    let route = useRoute();
    // loading
    let isShowLoading = ref(true);
    // 
    let anthologyCut = ref(false);
    // 
    let videoExtend = ref(false);
    // url
    let videoUrl = ref("");
    // 
    let videoData = ref([]);

    // 
    getVideo().then((res) => {
      videoData.value = res.data;
      videoData.value.forEach((item) => {
        if (item.id === 1) {
          item.selected = true;
          videoUrl.value = item.url;
        } else {
          item.selected = false;
        }
      });
      console.log(videoData.value);
    });

    // loading
    setTimeout(() => {
      isShowLoading.value = false;
    }, 1800);

    // 
    const handleExtend = () => {
      anthologyCut.value = true;
      videoExtend.value = true;
    };

    // 
    const handleScale = () => {
      anthologyCut.value = false;
      videoExtend.value = false;
    };

    // 
    const handleChangeAnthology = (item) => {
      // url
      videoUrl.value = item.url;
      console.log(videoUrl.value);

      // 
      videoData.value.forEach((ele) => {
        ele.selected = false;
        if (ele.id === item.id) {
          ele.selected = true;
        }
      });
    };

    return {
      title: route.query.title,
      isShowLoading,
      videoData,
      handleExtend,
      anthologyCut,
      videoExtend,
      handleScale,
      handleChangeAnthology,
      videoUrl,
    };
  },
  components: {
    Loading,
    jsVideo,
  },
};
</script>

