<template>
  <div class="js-video" :class="videoExtend ? 'js-video-extend' : ''">
    <!--  -->
    <div id="mse"></div>
    <!-- / -->
    <div class="button">
      <!--  -->
      <div
        class="player-icons"
        :class="videoExtend ? '' : 'active'"
        @click="handleExtend"
      >
        <span class="qy-play-icon unfold-icon"></span>
      </div>
      <!--  -->
      <div
        class="player-icons"
        :class="videoExtend ? 'active' : ''"
        @click="handleScale"
      >
        <span class="qy-play-icon fold-icon"></span>
      </div>
    </div>
  </div>
</template>

<script>
import Player from "xgplayer";
import { onMounted } from "vue";

export default {
  props: ["videoExtend", "videoUrl"],
  setup(props, { emit }) {
    // 
    onMounted(() => {
      new Player({
        id: "mse",
        url: props.videoUrl,
        // 
        fluid: true,
        // 
        keyShortcut: "on",
        videoInit: true,
      });
    });

    // 
    const handleExtend = () => {
      emit("handleExtend");
    };

    // 
    const handleScale = () => {
      emit("handleScale");
    };

    return {
      handleExtend,
      handleScale,
    };
  },
};
</script>

<style></style>
